import React from 'react'
import PropTypes from 'prop-types'

const LevelProgress = ({ title, items }) => {
  return (
    <div className="right w-1/2 pl-[50px]">
      <div className="tokyo_section_title float-left clear-both mb-[40px] h-auto w-full">
        <h3 className="text-[20px] font-bold">{title}</h3>
      </div>
      <div className="tokyo_progress">
        {items.map((skill) => (
          <div className="progress_inner" key={skill.name}>
            <span>
              <span className="label">{skill.name}</span>
              <span className="number">{skill.level}%</span>
            </span>
            <div className="background">
              <div className="bar">
                <div className="bar_in" style={{ width: `${skill.level}%` }} />
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

LevelProgress.propTypes = {
  title: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      level: PropTypes.number.isRequired
    })
  ).isRequired
}

export default LevelProgress
