import React from 'react'
import PropTypes from 'prop-types'
import LevelProgrees from '../../components/LevelProgrees'

const Languages = ({ programmingLanguages, languages }) => {
  return (
    <div className="tokyo_tm_progressbox float-left clear-both h-auto w-full bg-white pb-[100px] pl-[0px] pr-[0px] pt-[93px]">
      <div className="container">
        <div className="in float-left clear-both flex h-auto w-full">
          <LevelProgrees title={'Programming'} items={programmingLanguages} />
          <LevelProgrees title={'Languages'} items={languages} />
        </div>
      </div>
    </div>
  )
}

Languages.propTypes = {
  programmingLanguages: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      level: PropTypes.number.isRequired
    })
  ).isRequired,
  languages: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      level: PropTypes.number.isRequired
    })
  ).isRequired
}
export default Languages
