import React from 'react'
import PropTypes from 'prop-types'
import { StaticImage } from 'gatsby-plugin-image'

const StyledList = ({ title, items }) => {
  return (
    <div className="right w-1/2 pl-[50px]">
      <div className="tokyo_section_title float-left clear-both mb-[40px] h-auto w-full">
        <h3 className="text-[20px] font-bold">{title}</h3>
      </div>
      <div className="tokyo_tm_skill_list float-left clear-both h-auto w-full">
        <ul className="m-0 list-none">
          {items.map((item, index) => (
            <li className="relative m-0 pl-[25px]" key={index}>
              <StaticImage
                src={'../images/svg/rightarrow.svg'}
                alt={'pointer'}
                className="svg absolute left-0 top-1/2 h-[10px] w-[10px] translate-y-[-50%] text-white"
              />
              {item}
            </li>
          ))}
        </ul>
      </div>
    </div>
  )
}

StyledList.propTypes = {
  title: PropTypes.string.isRequired,
  items: PropTypes.array.isRequired
}

export default StyledList
