import React from 'react'
import PropTypes from 'prop-types'

const DetailsColumns = ({ items }) => {
  const firstColumnItems = items.slice(0, items.length / 2)
  const secondColumnItems = items.slice(items.length / 2)

  return (
    <div className="tokyo_tm_short_info float-left clear-both mb-[40px] flex h-auto w-full border-b border-solid border-[#DFDFDF] pb-[30px]">
      <div className="left w-1/2 pr-[50px]">
        <div className="tokyo_tm_info float-left clear-both h-auto w-full">
          <ul className="m-0 list-none">
            {firstColumnItems.map((item) => (
              <li className="m-0" key={item.id}>
                <span className="float-left mr-[10px] min-w-[100px] font-bold text-black">
                  {item.label}:
                </span>
                <span>{item.value}</span>
              </li>
            ))}
          </ul>
        </div>
      </div>
      <div className="right w-1/2 pl-[50px]">
        <div className="tokyo_tm_info">
          <ul className="m-0 list-none">
            {secondColumnItems.map((item) => (
              <li className="m-0" key={item.id}>
                <span className="float-left mr-[10px] min-w-[100px] font-bold text-black">
                  {item.label}:
                </span>
                <span>{item.value}</span>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  )
}

DetailsColumns.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      label: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired
    })
  ).isRequired
}

export default DetailsColumns
