import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import SectionContainer from '../../components/SectionContainer'
import SectionTitle from '../../components/SectionTitle'
import Introduction from '../about/Introduction'
import DetailsColumns from '../about/DetailsColumns'
import DownloadButton from '../../components/DownloadButton'
import Languages from '../about/Languages'
import InterestsGroups from '../about/InterestsGroups'
import { StaticImage } from 'gatsby-plugin-image'
import Experience from '../about/Experience'

const About = () => {
  const { authorJson, careerJson } = useStaticQuery(graphql`
    query {
      authorJson {
        fullName
        displayName
        summary
        roles
        positions
        technologies
        current {
          workplace {
            name
            position
          }
        }
        address {
          city
          country
        }
        hobbies
        contacts {
          email
        }
        education {
          id
          school
          speciality
          degree
          startDate
          endDate
        }
        programming_languages {
          name
          level
        }
        languages {
          name
          level
        }
        tech_groups
        interests_groups
      }
      careerJson {
        path {
          id
          organization
          position
          responsibilities
          startDate
          technologies
          url
          endDate
          description
        }
      }
    }
  `)

  const facts = [
    {
      id: 'company',
      label: 'Company',
      value: authorJson.current.workplace.name
    },
    {
      id: 'position',
      label: 'Position',
      value: authorJson.current.workplace.position
    },
    { id: 'email', label: 'Email', value: authorJson.contacts.email },
    {
      id: 'location',
      label: 'Location',
      value: `${authorJson.address.city}, ${authorJson.address.country}`
    },
    {
      id: 'degree',
      label: 'Degree',
      value: authorJson.education[0].degree
    },
    {
      id: 'speciality',
      label: 'Speciality',
      value: authorJson.education[0].speciality
    }
  ]

  return (
    <SectionContainer name={'about'}>
      <div className="container">
        <div className="tokyo_tm_about float-left clear-both h-auto w-full px-0 py-[100px]">
          <div className="tokyo_tm_title float-left clear-both mb-[62px] h-auto w-full">
            <div className="title_flex clear-both flex h-auto w-full items-end justify-between">
              <SectionTitle pageName={'About'} title={'About Me'} />
            </div>
          </div>
          <Introduction
            imageNode={
              <StaticImage
                className="min-w-full"
                src={'../../images/aboutme.jpg'}
                alt={authorJson.displayName}
              />
            }
            name={authorJson.fullName}
            position={authorJson.positions.join(' | ')}
            bio={[
              authorJson.roles.join('. '),
              authorJson.summary,
              `My current position is ${authorJson.current.workplace.position} in  ${authorJson.current.workplace.name}.`,
              `Skill Set: ${authorJson.technologies.join(', ')}.`
            ]}
          />
          <DetailsColumns items={facts} />
          <DownloadButton text={'Download CV'} url={'../downloads/cv.pdf'} />
        </div>
      </div>
      <Languages
        programmingLanguages={authorJson.programming_languages}
        languages={authorJson.languages}
      />
      <InterestsGroups
        interests={authorJson.interests_groups}
        knowledge={authorJson.tech_groups}
      />
      <Experience
        education={authorJson.education}
        experiences={careerJson.path}
      />
    </SectionContainer>
  )
}

export default About
