import React from 'react'
import PropTypes from 'prop-types'
import ExperienceProgress from '../../components/ExperienceProgress'

const Experience = ({ education, experiences }) => {
  return (
    <div className="tokyo_tm_resumebox float-left clear-both h-auto w-full bg-white px-0 py-[93px]">
      <div className="container">
        <div className="in float-left clear-both flex h-auto w-full">
          <ExperienceProgress
            title={'Education'}
            items={experiences.map((experience) => ({
              id: experience.id,
              startDate: experience.startDate,
              endDate: experience.endDate,
              name: experience.organization,
              description: experience.position[0]
            }))}
          />
          <ExperienceProgress
            title={'Education'}
            items={education.map((edu) => ({
              id: edu.id,
              startDate: edu.startDate,
              endDate: edu.endDate,
              name: edu.school,
              description: edu.degree
            }))}
          />
        </div>
      </div>
    </div>
  )
}

Experience.propTypes = {
  education: PropTypes.array.isRequired,
  experiences: PropTypes.array.isRequired
}

export default Experience
